@charset "utf-8";
@import "./App.sass";

#search-hero-button, .hero input {
  background-color: $white;
  border: none; }

ul {
  color: #4a4a4a;
  margin-left: 10px; }

#search-hero-button {
  font-weight: 600;
  color: $white;
  background-color: $turquoise;
  border: 1px solid $white;
  &:hover {
    background-color: $white;
    color: $turquoise; } }

.level-item h5 {
  margin-bottom: 8px; }
