body {
  margin: 0;
  color: #444444;
  font-family: "Source Sans Pro";
  font-size: 14px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  font-family: "Source Sans Pro";
  font-size: 34px;
  font-weight: bold;
  letter-spacing: 0;
  line-height: 42px;
}

h2 {
  font-family: "Source Sans Pro";
  font-size: 25px;
  font-weight: bold;
  letter-spacing: 0;
  line-height: 32px;
}

h3 {
  font-family: "Source Sans Pro";
  font-size: 21px;
  font-weight: bold;
  letter-spacing: 0;
  line-height: 27px;
  margin-bottom: 10px;
}

h4 {
  font-family: "Source Sans Pro";
  font-size: 21px;
  font-weight: 600;
  letter-spacing: 0;
  line-height: 27px;
}

h5 {
  font-family: "Source Sans Pro";
  font-size: 18px;
  font-weight: 600;
  letter-spacing: 0;
  line-height: 20px;
}

h6 {
  font-family: "Source Sans Pro";
  font-size: 16px;
  font-weight: bold;
  letter-spacing: 0;
  line-height: 17px;
}

#root {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

.section {
  flex: 1 1;
}
